body {
  margin: 0;
  padding: 30px;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.app {
  display: flex;
  justify-content: center;
  align-items: center;
}

/** TOASTER **/

.toaster .toaster__message {
  pointer-events: auto;
}

.toaster .toaster_screen-top-left {
  max-width: 560px;
  position: fixed;
  z-index: 5500;
  pointer-events: none;
  top: 0px;
  left: 0px;
}

.toaster .toaster_screen-top {
  max-width: 560px;
  position: fixed;
  z-index: 5500;
  pointer-events: none;
  margin: 0px auto;
  text-align: center;
  top: 0px;
  right: 0px;
  left: 0px;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.toaster .toaster_screen-top-right {
  max-width: 560px;
  position: fixed;
  z-index: 5500;
  pointer-events: none;
  top: 0px;
  right: 0px;
}

.toaster .toaster_screen-bottom-left {
  max-width: 560px;
  position: fixed;
  z-index: 5500;
  pointer-events: none;
  bottom: 0px;
  left: 0px;
}

.toaster .toaster_screen-bottom {
  max-width: 560px;
  position: fixed;
  z-index: 5500;
  pointer-events: none;
  margin: 0px auto;
  text-align: center;
  bottom: 0px;
  right: 0px;
  left: 0px;
  display: flex;
  flex-direction: column-reverse;
  align-items: center;
}

.toaster .toaster_screen-bottom-right {
  max-width: 560px;
  position: fixed;
  z-index: 5500;
  pointer-events: none;
  bottom: 0px;
  right: 0px;
}

/** ALERT **/

.alert {
  width: 160px;
  max-width: 160px;
  position: relative;
  padding: 0.75rem 1.25rem;
  margin-bottom: 1rem;
  border: 1px solid transparent;
  border-radius: 0.25rem;
  color: #004085;
  background-color: #cce5ff;
  border-color: #b8daff;
}

.alert.alert-primary {
  color: #004085;
  background-color: #cce5ff;
  border-color: #b8daff;
}
.alert.alert-secondary {
  color: #383d41;
  background-color: #e2e3e5;
  border-color: #d6d8db;
}
.alert.alert-success {
  color: #155724;
  background-color: #d4edda;
  border-color: #c3e6cb;
}
.alert.alert-danger {
  color: #721c24;
  background-color: #f8d7da;
  border-color: #f5c6cb;
}
.alert.alert-warning {
  color: #856404;
  background-color: #fff3cd;
  border-color: #ffeeba;
}
.alert.alert-info {
  color: #0c5460;
  background-color: #d1ecf1;
  border-color: #bee5eb;
}
.alert.alert-light {
  color: #818182;
  background-color: #fefefe;
  border-color: #fdfdfe;
}
.alert.alert-dark {
  color: #1b1e21;
  background-color: #d6d8d9;
  border-color: #c6c8ca;
}

.alert .close {
  position: absolute;
  top: 0;
  right: 0;
  z-index: 2;
  padding: 0.9rem;
  color: inherit;
  background-color: transparent;
  border: 0;
}
